@import url("https://fonts.googleapis.com/css2?family=Jost:ital,wght@1,300&family=Mulish&display=swap");

* {
  padding: 0;
  margin: 0;
  font-family: "Jost", sans-serif;
}

body {
  background: #f3f2ef;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100vh;
  width: 100vw;
  font-family: "Jost", sans-serif;
}

html {
  font-size: 100%;
  height: 100%;
  background: #eee;
}

.box {
  width: 25vw;
  height: 60vh;
  border-radius: 0.5rem;
  box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
  background: #a5bbdd;
  position: relative;
  overflow: hidden;
  transition: translate3d(0, 0, 0);
  min-width: 20rem;
  max-height: 35rem;
}
.info {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 60vh;
  z-index: 4;
}
.location {
  margin-top: -5.5rem;
  text-align: center;
  font-weight: 800;
  font-size: 5rem;
  text-transform: capitalize;
}

.locationicon {
  font-size: 4rem !important;
}
.temp {
  margin-top: 2.5rem;
  text-align: center;
  font-size: 3rem;
}

form {
  display: flex;
  font-size: 1.5rem;
}

.tempmin_max {
  text-align: center;
  margin-top: 0.3rem;
  font-weight: 300;
  font-size: 1.2rem;
  color: #57606f;
}

.wave {
  opacity: 0.3;
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -20rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3000ms infinite linear;
  z-index: 1;
}

.wave-three {
  animation: drift 5000ms infinite linear;
  z-index: 2 !important;
  opacity: 0.2;
}

.wave-two {
  animation: drift 7000ms infinite linear;
  opacity: 0.1;
  z-index: 3 !important;
}

@keyframes drift {
  from {
    transform: rotate(0deg);
  }
  from {
    transform: rotate(360deg);
  }
}

h2 .locationicon {
  margin-right: 1rem !important;
}

@media (max-width: 600px) {
  .box {
    width: 90vw;
    height: 80vh;
  }

  .wave {
    top: 85%;
  }

  .info {
    font-size: 1.5em;
  }
}

@media (max-width: 500px) {
  .box {
    height: 80vh;
  }

  .wave {
    top: 115%;
  }
}

body > span {
  width: 100vw;
  text-align: center;
  color: grey;
}
.errorMsg {
  text-align: center;
  font-size: 2rem;
  margin: 2rem auto;
}
